/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}


/* #####################
#####################
#####################
About
#####################
#####################
##################### */
.about{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(21, 23, 31, 0.579);
  backdrop-filter: blur(3px);
  content: '';
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #282c34;
}
.about .about-card{
  width: 90%;
  max-width: 700px;
  height: 70vh;
  max-height: 80vh;
  background-color: white;
  padding: 16px;
  border-radius: 8px;
}

.about .about-card .header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}
.about .about-card .header h3{
  margin: 0;
}
.about .about-card .header .close{
  width: 50px;
  padding: 12px;
  cursor: pointer;
}
.about .about-card .header .close:hover{
  background-color: rgb(255, 239, 239);
  border-radius: 4px;
}
.about .about-card .body{
  padding: 16px;
}
.about .about-card .body ul{
  list-style-type: square;
}
.about .about-card .body  ul li{
  margin-bottom: 4px;
}